import navStyle from './Navbar.module.css'
import {NavLink} from "react-router-dom";

function Navbar() {
    return (
        <nav className={navStyle.nav}>
            <NavLink className={navStyle.navLinkItem} to="/profile" activeClassName={navStyle.activeLink}>Profile</NavLink>
            <NavLink className={navStyle.navLinkItem} to="/dialogs" activeClassName={navStyle.activeLink}>Messages</NavLink>
            <NavLink className={navStyle.navLinkItem} to="/news" activeClassName={navStyle.activeLink}>News</NavLink>
            <NavLink className={navStyle.navLinkItem} to="/music" activeClassName={navStyle.activeLink}>Music</NavLink>
            <NavLink className={navStyle.navLinkItem} to="/settings" activeClassName={navStyle.activeLink}>Settings</NavLink>
        </nav>
    );
}

export default Navbar;
